/* styles.css */
.t-cedarville-cursive {
    font-family: "Dancing Script", cursive; /* Added fallback */
    font-weight: 400;
    font-size: 1.5em;
    font-style: normal;
    text-shadow: 
    2px 2px 4px rgba(0, 0, 0, 0.6),  /* More pronounced first shadow */
    0 0 30px rgba(0, 0, 0, 0.4),    /* Larger blur for the second shadow */
    0 0 10px rgba(0, 0, 0, 0.5);
  }

  @import url("https://fonts.googleapis.com/css2?family=Poppins:wght@500;600;700&display=swap");
@import url('https://fonts.googleapis.com/css2?family=Roboto+Mono:ital,wght@0,100..700;1,100..700&display=swap');

  .cardbook {
    margin-right: 18rem;
    width: auto;
    height: auto;
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 20px;
    border-radius: 20px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    color: black;
}

.cardbook h2 {
    margin-bottom: 10px;
}

.form {
    display: flex;
    flex-direction: column;
    width: 100%;
}

.form input {
    padding: 10px;
    border-radius: 5px;
    border: 1px solid #DDDDDD;
    color: #A0A6A3;
    font-family: "Roboto Mono", sans-serif;
    box-shadow: 1px 5px 9px rgba(211, 211, 211, .7);
    margin-bottom: 15px; /* Add margin for spacing */
}

.login_btn {
    font-size: 20px;
    color: white;
    border-radius: 15px;
    border: none;
    background: linear-gradient(-90deg, #003A74, #006AD5);
    width: 100%;
    padding: 10px;
    margin-top: 15px;
    margin-bottom: 15px;
    box-shadow: 1px 5px 9px rgba(211, 211, 211, .9);
    cursor: pointer; /* Change cursor on hover */
}

  